main  {
  position: relative;
  height: 100dvh;
  display: grid;
  place-items: center;
}

.container {
  width: min(100% - 30px, 1080px);
  margin-inline:auto;
}

h1, h2, h3, p, a, button {
  color: #fff;
  font-family: 'Poppins', sans-serif;
}

.grid {
  display: grid;
}

.flex {
  display: flex;
}

.center {
  place-items: center;
}

.star {
  cursor: pointer;
  font-size: 80px;
}

input[type=radio] {
  display: none;
}

.remarks {
  height: 40px;
  margin-top: 20px;
  text-align: center;
}

.dialog {
  position: absolute;
  height: 350px;
  width: 350px;
  padding: 20px;
  background: #eaeaea url(../img/logo512.png) center 10% / cover no-repeat;
}

button {
  width: 150px;
  height: 40px;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 600;
}

.close {
  position: absolute;
  right: 20px;
  top: 20px;
  background-color: #e15945;
}

.close:hover {
  background-color: #d34a3e;
}

.open {
  position: fixed;
  right: 25px;
  bottom: 40px;
  background-color: #2b58de;
}

.open:hover {
  background-color: #214cce;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99;
  background-color: rgb(0 0 0 / 0.5);
}